import React from 'react';
import {cn} from '../../lib/classname';
import {Card} from '../card';
import {emptyCard} from '../card/card.mock';

import './index.styl';
import {ExamAddCard} from '../card/exam-add-card';

const b = cn('cards-container');

export enum CardType {
    exams = 'exams',
    default = 'default',
}


interface CardsContainerProps {
    cards: Card[];
    containerName: string;
    onLinkClick: () => void;
    className?: string;
    type: CardType
}

export const CardsContainer = ({
    cards,
    containerName,
    onLinkClick,
    type,
    className = '',
}: CardsContainerProps) => {

    const DopCard = () => {
        switch (type) {
            case CardType.default: {
                const allCard = cards.reduce((allCard, card) => {
                    allCard.count += card.count;
                    allCard.time += card.time;
                    allCard.progress += (card.progress / cards.length);
                    return allCard;
                }, emptyCard);

                return(
                    <Card key={allCard.id}
                          {...allCard}
                          className={className}
                    />
                )
            }
            case CardType.exams: {
                return(
                    <ExamAddCard />
                )
            }
        }
    };

    return(
        <div className={b()}>
            <div className={b('title')}>
                <h1 className={b('title-1')}>{containerName}</h1>
                <div
                    className={b('link')}
                    onClick={onLinkClick}
                >
                     
                </div>
            </div>
            <div className={b('cards')}>
                {cards.map((card) => (
                    <Card
                        {...card}
                        key={card.id}
                        className={className}
                        type={type}
                    />
                ))}
                {DopCard()}
            </div>
        </div>
    )
}

try {
    // @ts-ignore
    CardsContainer.displayName = "CardsContainer";
    // @ts-ignore
    CardsContainer.__docgenInfo = { "description": "", "displayName": "CardsContainer", "props": { "cards": { "defaultValue": null, "description": "", "name": "cards", "required": true, "type": { "name": "any[]" } }, "containerName": { "defaultValue": null, "description": "", "name": "containerName", "required": true, "type": { "name": "string" } }, "onLinkClick": { "defaultValue": null, "description": "", "name": "onLinkClick", "required": true, "type": { "name": "() => void" } }, "className": { "defaultValue": { value: "" }, "description": "", "name": "className", "required": false, "type": { "name": "string" } }, "type": { "defaultValue": null, "description": "", "name": "type", "required": true, "type": { "name": "CardType" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/cards-container/index.tsx#CardsContainer"] = { docgenInfo: CardsContainer.__docgenInfo, name: "CardsContainer", path: "src/components/cards-container/index.tsx#CardsContainer" };
}
catch (__react_docgen_typescript_loader_error) { }