import React from 'react';
import {cn} from '../../lib/classname';

import './index.styl';
import {Progress} from "../progress";
import {CardType} from "../cards-container";
import {Link} from "react-router-dom";
import {Definition} from "../definition";

const b = cn('card');

export enum CardBackground {
    blue = 'blue',
    orange = 'orange',
    peach = 'peach',
    grey = 'grey',
    pink = 'pink',
    fiolet = 'fiolet'
}

export enum TextColor {
    white = 'white',
    black = 'black'
}

export interface Card {
    count: number;
    time?: number;
    title: string;
    background?: CardBackground;
    id: string;
    className?: string;
    color?: TextColor;
    progress?: number;
    type: CardType;
    definitions: Definition[];
    selected: string[];
}

export const Card = ({
    count,
    time,
    title,
    background = CardBackground.fiolet,
    color = TextColor.black,
    progress,
    type,
    id,
}: Card) => (
    <Link className={b({background, color})} to={`${type === CardType.exams ? 'exam' : 'card'}/${id}`}>
        {progress &&
            <Progress
                percent={progress}
                className={b('progress')}
            />
        }
        <h1 className={b('title')}>{title}</h1>
        <div className={b('description')}>
            {count}  {time === undefined ? null : `| ${time} min`}
        </div>
    </Link>
);

try {
    // @ts-ignore
    Card.displayName = "Card";
    // @ts-ignore
    Card.__docgenInfo = { "description": "", "displayName": "Card", "props": { "count": { "defaultValue": null, "description": "", "name": "count", "required": true, "type": { "name": "number" } }, "time": { "defaultValue": null, "description": "", "name": "time", "required": false, "type": { "name": "number" } }, "title": { "defaultValue": null, "description": "", "name": "title", "required": true, "type": { "name": "string" } }, "background": { "defaultValue": null, "description": "", "name": "background", "required": false, "type": { "name": "CardBackground" } }, "id": { "defaultValue": null, "description": "", "name": "id", "required": true, "type": { "name": "string" } }, "className": { "defaultValue": null, "description": "", "name": "className", "required": false, "type": { "name": "string" } }, "color": { "defaultValue": null, "description": "", "name": "color", "required": false, "type": { "name": "TextColor" } }, "progress": { "defaultValue": null, "description": "", "name": "progress", "required": false, "type": { "name": "number" } }, "type": { "defaultValue": null, "description": "", "name": "type", "required": true, "type": { "name": "any" } }, "definitions": { "defaultValue": null, "description": "", "name": "definitions", "required": true, "type": { "name": "any[]" } }, "selected": { "defaultValue": null, "description": "", "name": "selected", "required": true, "type": { "name": "string[]" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/card/index.tsx#Card"] = { docgenInfo: Card.__docgenInfo, name: "Card", path: "src/components/card/index.tsx#Card" };
}
catch (__react_docgen_typescript_loader_error) { }