import React, {useState} from 'react';
import {cn} from '../../lib/classname';

import {Definition} from "../definition";
import {Card} from '../card';

import './index.styl';
import {Link} from 'react-router-dom';

const b = cn('definitions-container');

interface DefinitionsContainerProps {
    card: Card;
    onClick: () => void;
}

export const DefinitionsContainer = ({
    card,
    onClick,
}: DefinitionsContainerProps) => {
    console.log('card', card.selected);
    const [checkedCards, setCheckedCards] = useState(new Set());

    const handleClick = (id: string) => {
        const cards = new Set([...checkedCards]);
        if (cards.has(id)) {
            cards.delete(id);
            setCheckedCards(cards);
            return;
        }
        cards.add(id);
        setCheckedCards(cards);
    };

    return(
        <div className={b({color: card.color})}>
            <div className={b('title', {background: card.background})}>
                <div className={b('info')}>
                    <h1>{card.title}</h1>
                    <div className={b('about')}>  {card.progress}%, {card.count} </div>
                </div>
                <Link className={b('link')}
                     onClick={onClick}
                     to={`/card/${card.id}/train/0`}
                >
                    {checkedCards.size === 0 ? ' ' : ' '}
                </Link>
            </div>
            <div className={b('definitions')}>
                {card.definitions.map((el) => (
                    <Definition {...el} key={el.id}
                                onClick={handleClick}
                                checked={card.selected.some((id) => id === el.id)}
                    />
                ))}
            </div>
        </div>
    )
};
try {
    // @ts-ignore
    DefinitionsContainer.displayName = "DefinitionsContainer";
    // @ts-ignore
    DefinitionsContainer.__docgenInfo = { "description": "", "displayName": "DefinitionsContainer", "props": { "card": { "defaultValue": null, "description": "", "name": "card", "required": true, "type": { "name": "any" } }, "onClick": { "defaultValue": null, "description": "", "name": "onClick", "required": true, "type": { "name": "() => void" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/definitions-container/index.tsx#DefinitionsContainer"] = { docgenInfo: DefinitionsContainer.__docgenInfo, name: "DefinitionsContainer", path: "src/components/definitions-container/index.tsx#DefinitionsContainer" };
}
catch (__react_docgen_typescript_loader_error) { }